  margin: 0;
  line-height: 1; }

#page-main {
  height: 100vh; }
  #page-main #page-wrapper {
    padding: 25px;
    height: 100%; }
  #page-main #page-title {
    padding-bottom: 30px;
    text-align: center; }
  #page-main #page-content {
    display: flex;
    height: 100%;
    overflow: hidden; }
    #page-main #page-content .right {
      width: 70%;
      min-width: 745px;
      padding: 0 0 0 20px; }
      #page-main #page-content .right iframe {
        width: 100%;
        height: 100%; }
    #page-main #page-content .left {
      display: flex;
      flex-direction: column;
      width: 30%;
      padding: 0 20px 0 0; }
      #page-main #page-content .left ul {
        padding: 0 0 0 25px;
        margin: 0; }
        #page-main #page-content .left ul ul {
          padding: 10px 0 0 25px; }
      #page-main #page-content .left li {
        margin-bottom: 10px; }
      #page-main #page-content .left #gdpr-toc {
        display: flex;
        flex-direction: column;
        overflow: hidden; }
        #page-main #page-content .left #gdpr-toc .content {
          overflow-y: scroll;
          overflow-x: hidden; }
          #page-main #page-content .left #gdpr-toc .content > ul > li > a {
            font-weight: 700;
            background-color: #ffe600; }
      #page-main #page-content .left #gdpr-lang {
        margin: 15px 0; }
        #page-main #page-content .left #gdpr-lang select {
          width: 100%;
          margin: 15px 0; }
